.search-bar {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50px;
  margin-top: 20px;
}

.search-bar input {
  background: #0076b53a;
  border-radius: 10px;
  border: 0;
  color: white;
  font-size: 1.25rem;
  font-weight: 700;
  height: 100%;
  padding: 0 20px;
  width: clamp(263px, 70%, 940px);
}

.search-bar input::placeholder {
  color: white;
}
.search-bar input:hover,
.search-bar input:focus {
  transition: 0.5s ease;
  background: rgba(0, 118, 181, 0.803);
  outline: none;
}

@media screen and (max-width: 263px) {
  .search-bar input {
    width: 100%;
  }
}
