nav {
  background: #0076b572;
  border-radius: 20px;
  margin: 20px auto;
  width: clamp(280px, 100%, 300px);
  height: 50px;
  display: flex;
}
nav ul {
  width: 100%;
  display: flex;
  list-style: none;
  align-self: center;
  justify-content: space-evenly;
}
nav ul li {
  padding: 5px 1.25em;
  text-align: center;
}

nav ul a {
  color: white;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
}

nav ul li:hover {
  background: rgba(0, 166, 255, 0.75);
  border-radius: 20px;
  transition: 0.5s;
}

@media screen and (max-width: 279px) {
  nav {
    height: 100%;
    width: 100%;
    flex-direction: column;
    overflow: scroll;
  }
  nav ul {
    flex-direction: column;
  }
}
