main {
  width: 100%;
  height: calc(100vh - 20px - 50px - 20px - 50px - 20px);
  max-height: calc(100vh - 20px - 50px - 20px - 50px - 20px);
  display: flex;
  justify-content: center;
}
img {
  display: block;
}
.photo-gallery {
  overflow: scroll;
  max-height: 100%;
  width: clamp(400px, 56%, 1080px);
  border-top: 1px solid #00a4ff;
  background: #080f12;
  display: grid;
  gap: 20px;
  justify-content: space-evenly;
  padding: 46px 0px;
}
.photo {
  justify-self: center;
  position: relative;
  height: 205px;
  width: 144px;
  overflow: hidden;
}
.photo:hover .photo-description {
  opacity: 1;
}
.photo:hover img {
  transition: transform 1s ease;
}

.photo-description {
  position: absolute;
  top: 0;
  background: rgba(0, 0, 0, 0.8); /* Black see-through */
  color: #f1f1f1;
  width: 100%;
  height: 100%;
  transition: 0.5s ease;
  opacity: 0;
  padding: 5px;
  color: white;
  border: 1px solid #00a4ff;
}

.rating {
  display: flex;
}
.photo-description .rating p {
  margin-left: 5px;
  color: #ffbc05;
  font-weight: 700;
}
.rating img {
  align-self: center;
}
.photo-description h2 {
  font-size: 12px;
  margin-bottom: 5px;
}
.photo-description p {
  font-size: 9px;
  color: rgba(255, 255, 255, 0.5);
}
.photo-description .description {
  margin-top: 5px;
  font-size: 9px;
  color: white;
}
.photo-gallery {
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
}
.photo-gallery::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}
@media screen and (min-width: 400px) {
  .photo-gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 900px) {
  .photo-gallery {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 1200px) {
  .photo-gallery {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (min-width: 1500px) {
  .photo-gallery {
    grid-template-columns: repeat(5, 1fr);
  }
}
